<template>
  <div class="pdf-iframe">
    <iframe :src="html" id="pdf_iframe"></iframe>
  </div>
</template>

<script>
import Loading from '../Shimmer/Loading.vue'

export default {
  components: { Loading },
  
  name: 'PdfIframe',
  data() {
    return {
      show: false,
      html: ''
    }
  },
  methods: {
    locationData(v, html) {
      this.html = html
      this.show = v
    },
    printPDF() {
      try {

        const _dom = window.parent.document.getElementById('pdf_iframe')
        
        _dom.contentWindow.printPDF()
      } catch (err) {
        
      }
    }
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"
@import "@/common/sass/pdf.sass"
.d-none 
  display: none !important

.pdf-iframe 
  position: fixed
  left: 15px
  top: 100vh
  border-radius: 3px

.pdf-iframe, .pdf-iframe iframe
  width: calc( 100vw - 30px )
  height: 20vh
  border: none !important

</style>


