const buildTitle = function(txtList) {
  return `關於${txtList}的幫助`
}

const buildHtml = function(txtList) {
  let html = `<ul style="font-size: 16px">`

  for (const i of txtList) {
    html +=
    `<li class="mb-2">${i}</li>`
  }
  html += `</ul>`

  return html
}

