const helpTitleMap = {
  'prices_input': buildTitle('報價單價'),
  'listing_search': buildTitle('發票的搜索功能'),
  'listing_timed_limit': buildTitle('發票的日期過濾'),
  'listing_plus_num_input': buildTitle('發票的菜品數量'),
  'listing_plus_price_input': buildTitle('發票的菜品單價'),
  'listing_plus_discount_input': buildTitle('發票的折扣')
}

const getHtml = function(help_code) {
  return helpHtmlMap[help_code]
}

const getTitle = function(help_code) {
  return helpTitleMap[help_code]
}

export default {
  getTitle,
  getHtml
}


